export default {
	data() {
		return {
			addressList: null,
			bottom: getApp().globalData.bottom,

		}
	},
	onLoad: function() {

	},
	onShow: function() {
		this.token = uni.getStorageSync('token')
		this.getList()
		
	},
	methods: {
		//获得地址列表
		getList: function() {
			var t = this;
			var data = {
				token: this.token
			}
			this.$urlRequest('addressList.do', {
				token: this.token
			}).then(res => {
				this.addressList = res.data
			})

		},
		//新增收获地址
		gotoDetail: function(e) {
			var index = e.currentTarget.dataset.index;
			var title = e.currentTarget.dataset.title;
			if (index != undefined) {
				uni.navigateTo({
					url: 'address_detail?title=' + title + '&address=' + JSON.stringify(this.addressList[index]),
				})
			} else {
				uni.navigateTo({
					url: 'address_detail?title=' + title,
				})
			}
		},
		//选择切换地址
		chooseMe:function(index,id){
			var data  = {
				token:this.token,
				id:id
			}
			this.$urlRequest('swapAddressList.do',data).then(res=>{
				if(res.code == '1'){
					this.$myToast('选择成功')
					setTimeout(function(){
						uni.navigateBack({
							delta:1
						})
					},1500)
				}else{
					this.$myToast(res.msg)
				}
				
			})
		}
	}
}
